* {
  box-sizing: border-box;
}

body {
  font-family: "Roboto", sans-serif;
  background-image: url("../images/bg.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  min-height: 100vh;
  padding: 0;
  margin: 0;
  color: #fff;
  overflow: hidden;
  position: relative;
}

img {
  max-width: 100%;
}

.container {
  width: 1280px;
  margin: 0 auto;
  position: relative;
}

.searchBar {
  background: #fff;
  color: #000;
  max-width: 650px;
  text-align: center;
  margin: 0 auto 40px;
  padding: 5px 10px;
  border-radius: 5px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.5);
}

.mdl-radio {
  margin-right: 20px;
}

.mdl-textfield {
  margin-right: 20px;
}

.mdl-textfield__input {
  padding: 9px 0;
}

.mdl-textfield__label {
  top: 30px;
}

.mdl-button--raised {
  color: #fff;
  background: rgb(63, 81, 181);
  width: 45px;
  min-width: 45px;
  padding: 0;
}

.mdl-button.mdl-button--raised:hover {
  background: rgb(63, 81, 181);
}

h3 {
  font-size: 30px;
  text-transform: uppercase;
  letter-spacing: 6px;
  font-weight: 500;
  margin: 10px 0 25px;
}

.weatherDetails {
  text-align: center;
  background: rgba(0, 0, 0, 0.78);
  margin: 0 auto;
  max-width: 650px;
  padding: 50px;
  border-radius: 5px;
}

.weatherDetails img {
  width: 80px;
  animation: trans 3s infinite;
}

@keyframes trans {
  0% {
    transform: rotateY(0deg) scale(1);
  }
  50% {
    transform: rotateY(180deg) scale(1.3);
  }
  100% {
    transform: rotateY(0deg) scale(1);
  }
}

.weather {
  position: relative;
  height: 100vh;
  padding-top: 50px;
}

.city-image {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  object-position: center;
  object-fit: cover;
}
